$start: 1; // Always 1
$end: 20; // Number of slides: 20 slides in this case

// slide
.slider {
  &__item {
    transition: all .4s;

    // Loop through .slider__item--(slide-number)
    @for $slide from $start through $end {

      // .slider__item--(slide-number)
      &--#{$slide} {

        // Within each .slider__item--(slide-number) loop through #slide-(number)
        @for $i from $start through $end {

          @if $i <= $slide - 3 {
            // For the slides 3 or more positions on the right
            // #slide-(number):checked ~ .slider__holder .slider__item--(slide-number)
            #slide-#{$i}:checked ~ .slider__holder & {
              z-index: -1;

              opacity: 0;

              transform: translateX(210px) scale(0.65);
            }
            #carousel-#{$i}:checked ~ .slider__holder & {
              z-index: -1;

              opacity: 0;

              transform: translateX(210px) scale(0.65);
            }
          }

          @if $i == $slide - 2 {
            // For the slide 2 positions on the right
            // #slide-(number):checked ~ .slider__holder .slider__item--(slide-number)
            #slide-#{$i}:checked ~ .slider__holder & {
              z-index: 0;

              transform: translateX(210px) scale(0.65);

              @media (max-width: 900px) {
                transform: translateX(170px) scale(0.65);
              }

              @media (max-width: 768px) {
                opacity: 0;
              }
            }
            #carousel-#{$i}:checked ~ .slider__holder & {
              z-index: 0;

              transform: translateX(210px) scale(0.65);

              @media (max-width: 900px) {
                transform: translateX(170px) scale(0.65);
              }

              @media (max-width: 768px) {
                opacity: 0;
              }
            }
          }

          @if $i == $slide - 1 {
            // For the slide 1 positions on the right
            // #slide-(number):checked ~ .slider__holder .slider__item--(slide-number)
            #slide-#{$i}:checked ~ .slider__holder & {
              z-index: 1;

              transform: translateX(100px) scale(0.85);

              @media (max-width: 768px) {
                opacity: 0.6;
              }
            }
            #carousel-#{$i}:checked ~ .slider__holder & {
              z-index: 1;

              transform: translateX(100px) scale(0.85);

              @media (max-width: 768px) {
                opacity: 0.6;
              }
            }
          }

          @if $i == $slide {
            // For the slide 1 positions under the active one
            // #slide-(number):checked ~ .slider__holder .slider__item--(slide-number)
            #slide-#{$i}:checked ~ .slider__holder & {
              position: relative;
              z-index: 2;

              transform: translate(0) scale(1);
            }
            #carousel-#{$i}:checked ~ .slider__holder & {
              position: relative;
              z-index: 2;

              transform: translate(0) scale(1);
            }
            #slide-#{$i}:checked ~ .slider__holder & .slider__text,
            #carousel-#{$i}:checked ~ .slider__holder & .slider__text {
              opacity: 1;
            }
          }

          @if $i == $slide + 1 {
            // For the slide 1 positions on the left
            // #slide-(number):checked ~ .slider__holder .slider__item--(slide-number)
            #slide-#{$i}:checked ~ .slider__holder & {
              z-index: 1;

              transform: translateX(-100px) scale(0.85);

              @media (max-width: 768px) {
                opacity: 0.6;
              }
            }
            #carousel-#{$i}:checked ~ .slider__holder & {
              z-index: 1;

              transform: translateX(-100px) scale(0.85);

              @media (max-width: 768px) {
                opacity: 0.6;
              }
            }
          }

          @if $i == $slide + 2 {
            // For the slide 2 positions on the left
            // #slide-(number):checked ~ .slider__holder .slider__item--(slide-number)
            #slide-#{$i}:checked ~ .slider__holder & {
              z-index: 0;

              transform: translateX(-210px) scale(0.65);

              @media (max-width: 900px) {
                transform: translateX(-170px) scale(0.65);
              }

              @media (max-width: 768px) {
                opacity: 0;
              }
            }
            #carousel-#{$i}:checked ~ .slider__holder & {
              z-index: 0;

              transform: translateX(-210px) scale(0.65);

              @media (max-width: 900px) {
                transform: translateX(-170px) scale(0.65);
              }

              @media (max-width: 768px) {
                opacity: 0;
              }
            }
          }

          @if $i >= $slide + 3 {
            // For the slide 3 positions or more on the left
            // #slide-(number):checked ~ .slider__holder .slider__item--(slide-number)
            #slide-#{$i}:checked ~ .slider__holder & {
              z-index: -1;

              opacity: 0;

              transform: translateX(-210px) scale(0.65);
            }
            #carousel-#{$i}:checked ~ .slider__holder & {
              z-index: -1;

              opacity: 0;

              transform: translateX(-210px) scale(0.65);
            }
          }

        }
      }
    }
  }
}

.slider__btn {
  // Loops through all the bullet points
  @for $bullet from $start through $end {
    // .bullets__item--(bullet-number)
    &--#{$bullet} {
      // #slide-(number):checked ~ .bullets .bullets__item--(bullet-number)
      #slide-#{$bullet}:checked ~ .slider__nav & {
        background: #2ABFBF;
        color: white;
      }
      #carousel-#{$bullet}:checked ~ .slider__nav & {
        background: #2ABFBF;
        color: white;
      }
    }
  }
}

.bullets {

  // .bullets__item
  &__item {
    transition: all .4s;

    // Loops through all the bullet points
    @for $bullet from $start through $end {
      // .bullets__item--(bullet-number)
      &--#{$bullet} {
        // #slide-(number):checked ~ .bullets .bullets__item--(bullet-number)
        #slide-#{$bullet}:checked ~ .bullets & {
          background: #2ABFBF;
        }
        #carousel-#{$bullet}:checked ~ .bullets & {
          background: #2ABFBF;
        }
      }
    }
  }
}

