@mixin d {
  @media only screen and (max-width: "1360px") {
    @content; } }
@mixin dt {
  @media only screen and (max-width: "1240px") {
    @content; } }
@mixin t {
  @media only screen and (max-width: "1023px") {
    @content; } }
@mixin m {
  @media only screen and (max-width: "767px") {
    @content; } }
@mixin s {
  @media only screen and (max-width: "474px") {
    @content; } }

@include font(ProximaNova-Regular, ProximaNova-Regular);
@mixin f {
  font-family: 'ProximaNova-Regular', serif; }
@include font(ProximaNova-Bold, ProximaNova-Bold);
@mixin fb {
  font-family: 'ProximaNova-Bold', serif; }
@include font(ProximaNova-SemiBold, ProximaNova-SemiBold);
@mixin fs {
  font-family: 'ProximaNova-SemiBold', serif; }
@include font(ProximaNova-Light, ProximaNova-Light);
@mixin fl {
  font-family: 'ProximaNova-Light', serif; }

@include font(Majesti-Banner-Bold, Majesti-Banner-Bold);
@mixin fmb {
  font-family: 'Majesti-Banner-Bold', serif; }

@mixin bs {
  box-shadow: 0 2px 5px rgba(black,.4); }
@mixin bs-lg {
  box-shadow: 0 2px 20px 0 rgba(146,146,146,0.50); }

$green: #2ABFBF;
$border: #cecece;
$orange: #F5A623;
$bg: #F5F7F9;
$cl: #4F575E;
$text: #4A4A4A;

body {
  min-width: 375px;
  @include f;
  font-size: 16px;
  color: $cl;
  line-height: 1.5;
  @include m {
    font-size: 14px; }
  &.no-scroll {
    overflow: hidden; } }
button,
input,
textarea,
select {
  @include f;
  font-size: 16px;
  @include m {
    font-size: 14px; } }
a {
  color: $green;
  text-decoration: none; }
strong,
b {
  @include fs;
  font-weight: 400; }
button {
  background: none; }

* {
 }  // box-shadow: 0 0 0 1px red

[data-animate] {
  opacity: 0;
  visibility: hidden;
  &.animated {
    opacity: 1;
    visibility: visible;
    animation-name: fadeInUp; } }

[data-animate-child] {
  opacity: 0;
  visibility: hidden;
  &.animated {
    opacity: 1;
    visibility: visible;
    animation-name: fadeInUp; } }

.animated {
  animation-duration: .4s;
  animation-color-mode: both;
  &.done {
    animation: none; } }

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translate3d(0, 40px, 0); }
  to {
    opacity: 1;
    transform: none; } }

.outer {
  overflow: hidden; }

// .center:not(.owl-item)
.center {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 50px;
  @include t {
    padding: 0 30px; }
  @include m {
    padding: 0 15px; }
  &_md {
    max-width: 1024px; }
  &_lg {
    max-width: 1480px; } }

.owl {
  &-dots {
    display: flex;
    margin-top: 30px;
    text-align: center;
    justify-content: center; }
  &-dot {
    width: 10px;
    height: 10px;
    margin: 0 6px;
    background: rgba(black,.1);
    border-radius: 50%;
    transition: background .2s;
    &.active {
      background: $green; } } }

.btn {
  padding: 10px 20px;
  background: $green;
  border: 2px solid transparent;
  border-radius: 22px;
  color: white;
  transition: background .2s, box-shadow .2s;
  line-height: 1.3;
  &:hover {
    background: darken($green,5);
    box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2), 0 1px 3px rgba(0, 0, 0, 0.05);
 }    // transform: translateY(-1px)
  &:active {
    box-shadow: none;
 }    // transform: translateY(0)
  span,
  .icon {
    display: inline-block;
    vertical-align: middle; }
  .icon {
    position: relative;
    top: 1px;
    margin-left: 10px;
    font-size: 12px;
    fill: white;
    &:first-child {
      margin: 0 10px 0 0; }
    &-send {
      font-size: 19px; }
    &-roof {
      top: -2px;
      font-size: 16px; } }
  &_stroke {
    background: transparent;
    border-color: $green;
    color: $green;
    &:hover {
      background: rgba($green,.1); }
    .icon {
      fill: $green; } }
  &_white {
    background: white;
    border-color: white;
    @include bs;
    color: $cl;
    &:hover {
      background: white; }
    .icon {
      fill: $cl; } }
  &_sm {
    padding: 5px 15px; }
  &_fixed {
    position: fixed;
    right: 30px;
    bottom: 30px;
    z-index: 49;
    @include t {
      right: 30px; }
    @include m {
      display: none; } }
  &_shake {
    animation: btnWiggle 5s infinite; }
  &_mt20 {
    margin-top: 20px; } }

@keyframes btnWiggle {
  0% {
    transform: rotate(0); }
  2% {
    transform: rotate(-1deg); }
  3.5% {
    transform: rotate(1deg); }
  5% {
    transform: rotate(0); }
  100% {
    transform: rotate(0); } }

a.btn {
  display: inline-block; }

.dropdown {
  position: relative;
  min-width: 60px;
  @include m {
    position: absolute;
    top: 136px;
    right: 15px; }
  &__head {
    position: relative;
    padding: 5px 30px 5px 10px;
    background: rgba(white,.8);
    border: 1px solid transparent;
    border-radius: 4px;
    color: $cl;
    cursor: default;
    transition: border-color .2s;
    @include m {
      background: #e2e5e6; }
    &:after {
      content: '';
      position: absolute;
      top: 14px;
      right: 10px;
      @include arr(8,6,$cl,b); } }
  &__drop {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    padding-top: 2px;
    opacity: 0;
    visibility: hidden;
    transition: opacity .2s, visibility .2s; }
  &__list {
    padding: 5px 0;
    background: white;
    border-radius: 4px;
    @include bs;
    @include m {
      background: #e2e5e6; } }
  &__item {
    display: flex;
    padding: 7px 10px;
    color: $cl;
    line-height: 1.3;
    transition: color .2s;
    align-items: center;
    @include nl {
      border-bottom: 1px solid $border; }
    &:hover {
      color: $green; } }
  &__preview {
    margin-right: 10px;
    flex: 0 0 32px; }
  &__pic {
    max-width: 100%;
    &_mb {
      position: relative;
      bottom: -5px; } }
  &__title {
    flex: 0 0 calc(100% - 42px); }
  &_online &__drop {
    left: auto;
    width: 240px; }
  &:hover &__head {
    &:after {
      transform: rotate(180deg); } }
  &:hover &__drop {
    opacity: 1;
    visibility: visible; } }

.header {
  padding-top: 48px;
  @include dt {
    padding: 0; }
  &__container {
