    padding: 5px 10px; }
  &__lang &__link.active &__preview {
    border-color: #2441AF;
    background: #D8D8D8; }
  &__lang &__preview,
  &__lang &__content {
    top: 15px; }
  &__lang &__preview {
    width: 20px;
    margin-right: 10px;
    padding: 2px;
    border: 1px solid transparent;
    border-radius: 2px;
    flex: 0 0 20px; }
  &__lang &__pic {
    border-radius: 1px; }
  &__lang &__label {
    font-size: 14px; }
  &__head {
    cursor: pointer; }
  &__link {
    display: flex;
    position: relative;
    min-height: 80px;
    padding: 5px 20px;
    background: $bg;
    border-radius: 4px;
    transform: translate3d(0,0,0) scale(1);
    transition: all .2s;
    align-items: flex-start;
    &:hover {
      background: white;
      box-shadow: 0 0 2px 0 rgba(150,148,148,0.50);
      transform: translate3d(0,0,0) scale(1.05);
      z-index: 2; } }
  &__preview,
  &__content {
    position: relative;
    top: 35px;
    transform: translateY(-50%); }
  &__preview {
    width: 60px;
    margin-right: 12px;
    flex: 0 0 60px; }
  &__pic {
    width: 100%; }
  &__content {
    color: $text;
    line-height: 1.3; }
  &__label {
    font-size: 18px; }
  &__text {
    margin-top: 1px;
    font-size: 14px; }
  &__drop {
    display: none;
    position: absolute;
    top: calc(100% + 12px);
    left: 50%;
    width: 420px;
    background: $bg;
    border-radius: 4px;
    box-shadow: 0 0 2px rgba(150,148,148,0.50);
    transform: translateX(-50%);
    &:before {
      content: '';
      position: absolute;
      top: -6px;
      left: calc(50% - 6px);
      width: 12px;
      height: 12px;
      background: $bg;
      pointer-events: none;
      box-shadow: 0 0 2px rgba(150,148,148,0.50);
      transform: rotate(45deg); }
    &.open {
      display: block; } }
  &__burger {
    display: none;
    margin: 0 -10px 0 auto;
    padding: 10px;
    font-size: 0;
    .icon {
      font-size: 20px;
      fill: white; }
    @include dt {
      display: block; } }
  &.fixed &__inner {
    @include dt {
      background: white;
      box-shadow: 0 2px 4px rgba(3,27,78,.1); } }
  &.fixed &__logo {
    @include dt {
      &:before {
        opacity: 0; }
      &:after {
        opacity: 1; } } }
  &.fixed &__burger {
    .icon {
      @include dt {
        fill: $text; } } }
  &.up &__container {
    @include dt {
      transform: translateY(-70px); } } }

@include rmin(1241) {
  .header {
    &.fixed &__container {
      position: fixed;
      top: -65px;
      transition: transform .4s; }
    &.fixed &__inner {
      background: white;
      box-shadow: 0 2px 4px rgba(3,27,78,.1); }
    &.fixed &__logo {
      width: 192px;
      height: 32px;
      &:before {
        opacity: 0; }
      &:after {
        opacity: 1; } }
    &.fixed &__drop {
      top: calc(100% + 8px); }
    &.fixed &__item {
      color: $text;
      .icon {
        fill: $text; } }
    &.fixed &__btn {
      padding: 7px 15px; }
    &.visible &__container {
      transform: translateY(65px); } } }

