.scroll {
  position: absolute;
  left: 50%;
  bottom: calc(6vw + 10px);
  left: 50%;
  width: 100px;
  margin-left: -50px;
  padding-top: 50px;
  font-size: 10px;
  color: white;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
  @include m {
    display: none; }
  .icon {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    font-size: 16px;
    fill: white;
    animation: mouse 2s linear infinite; }
  &_works {
    @include m {
      display: block;
      bottom: 10px;
      z-index: 4; }
    .icon {
      @include m {
        fill: black; } } }
  &_works &__title {
    @include m {
      display: none; } } }

@keyframes mouse {
  0% {
    top: 0;
    opacity: 0; }
  25% {
    opacity: 1; }
  75% {
    opacity: 0; }
  100% {
    top: 50%;
    opacity: 0; } }

