.cookies {
  position: fixed;
  bottom: 10px;
  left: 50%;
  max-width: 100%;
  padding: 5px 40px 5px 15px;
  background: white;
  @include bs;
  border-radius: 4px;
  text-align: center;
  transform: translateX(-50%);
  z-index: 999;
  @include m {
    left: 0;
    width: 100%;
    bottom: 0;
    padding: 10px 40px 10px 20px;
    border-radius: 0;
    font-size: 16px;
    transform: none; }
  a {
    display: inline-block; }
  &__close {
    position: absolute;
    top: 50%;
    right: 0;
    padding: 10px;
    margin-top: -17px;
    font-size: 0;
    @include m {
      right: 10px; }
    .icon {
      font-size: 14px; } } }

.app {
  display: flex;
  align-items: flex-start;
  @include t {
    display: block; }
  &__sidebar {
    min-height: 100vh;
    background: $bg;
    @include bs;
    flex: 0 0 30%;
    @include d {
      flex: 0 0 35%; } }
  &__nav {
    display: flex; }
  &__btn {
    padding: 20px 20px 10px;
    background: #35414A;
    flex: 0 0 50%;
    @include fs;
    color: #CFD3D6;
    text-align: center;
    &.active {
      background: $bg;
      color: $text;
      .icon {
        fill: $text; } }
    .icon {
      margin-bottom: 10px;
      font-size: 40px;
      fill: #CFD3D6; } }
  &__type {
    display: block; }
  &__section {
    display: none;
    padding: 30px;
    @include m {
      padding: 30px 15px; } }
  &__title {
    margin-bottom: 10px;
    @include fs;
    font-size: 16px; }
  &__text {
    margin-bottom: 30px;
    font-size: 13px; }
  &__head {
    margin-bottom: 30px;
    padding-bottom: 2px;
    border-bottom: 1px solid $border;
    @include fs;
    font-size: 16px;
    color: $green; }
  &__category {
    @include fs;
    color: $green; }
  &__field {
    margin-bottom: 20px; }
  &__flex {
    display: flex; }
  &__flex &__category {
    margin-bottom: 5px; }
  &__group {
    &:first-child {
      margin-right: 20px; } }
  &__file {
    padding: 2px 10px; }
  &__row {
    display: flex;
    margin: 0 -10px; }
  &__row &__field {
    padding: 0 10px;
    flex: 0 0 50%; }
  &__foot {
    margin-top: 20px;
    text-align: center;
    &_mt {
      margin-top: 40px; } }
  &__container {
    display: flex;
    position: relative;
    min-height: 100vh;
    padding: 30px;
    flex: 0 0 70%;
    flex-direction: column;
    @include d {
      flex: 0 0 65%; }
    @include t {
      min-height: 0; }
    @include m {
      padding: 30px 15px; } }
  &__top {
    margin-bottom: 40px;
    font-size: 0;
    text-align: right; }
  &__logo {
    display: inline-block;
    width: 240px;
    height: 40px;
    background: url(../img/weatherproof-family-black.svg) no-repeat 0 0/contain; }
  &__frame {
    flex: 1 0 auto; }
  &__inspiration {
    margin-top: 40px; }
  &__inspiration &__title {
    margin-bottom: 5px; }
  &__inspiration &__text {
    margin-bottom: 20px; }
  &__list {
    display: flex;
    margin: 0 -10px;
    flex-wrap: wrap; }
  &__preview {
    margin: 0 10px 20px;
    flex: 0 0 calc(20% - 20px);
    @include m {
      flex: 0 0 calc(50% - 20px); } }
  &__pic {
    display: block;
    width: 100%;
    border-radius: 2px;
    @include bs; } }

.love {
  text-align: center;
  &__theme {
    display: flex;
    align-items: center;
    justify-content: center;
    color: black;
    .icon {
      margin: 2px 5px 0 10px;
      font-size: 10px; } }
  &__bold {
    @include fs; }
  &__title {
    font-size: 24px;
    color: $text;
    @include m {
      font-size: 22px; } }
  &__black {
    @include fs;
    color: black; } }

.page {
  margin-bottom: calc(5vw + 70px);
  padding-top: 10px;
  overflow: hidden;
  &__slider {
    margin-bottom: 100px;
    @include m {
      margin-bottom: 60px; } }
  &__grid {
    position: relative;
    max-width: 100%;
    margin: 0;
    padding: 0;
    background: none;
    z-index: 2; } }

.about {
  margin-bottom: 100px;
  @include m {
    margin-bottom: 60px; }
  &__title {
    margin-bottom: 10px;
    @include fs; }
  &__tabs {
    border: 1px solid $border;
    @include m {
      margin: 0 -15px;
      border-width: 1px 0; } }
  &__nav {
    display: flex;
    margin: -1px -1px 0;
    border-bottom: 1px solid $border; }
  &__btn {
    margin-bottom: -1px;
    padding: 15px 20px;
    @include fs;
    color: $text;
    flex: 0 0 25%;
    transition: background .2s, color .2s;
    @include m {
      padding: 10px 15px;
      flex: 1 1 auto; }
    &.active {
      background: black;
      color: white; } }
  &__item {
    display: none;
    padding: 30px;
    @include m {
      padding: 15px; } }
  &__row {
    display: flex;
    margin: 0 -15px;
    @include m {
      display: block; } }
  &__col {
    padding: 0 15px;
    flex: 0 0 50%; }
  &__spec {
    margin-bottom: 5px; }
  &__type {
    @include fs;
    color: black; } }

.maylike {
  &__title {
    margin-bottom: 20px;
    padding-bottom: 5px;
    border-bottom: 1px solid $border;
    @include fs; }
  &__list {
    display: flex;
    margin: 0 -10px; }
  &__item {
    margin-bottom: 20px;
    padding: 0 10px;
    flex: 0 0 33.33%; }
  &__pic {
    display: block;
    width: 100%; } }







