.slider {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  &__radio {
    display: none;
  }

  &__holder {
    position: relative;

    width: 100%;
    max-width: 700px;
    margin: 0 auto;

    text-align: left;

    @media (max-width: 900px) {
      max-width: 540px;
    }

  }

  &__item {
    position: absolute;
    top: 0;
    left: 0;

    display: block;
    // overflow: hidden;

    width: 100%;

    opacity: 1;

    cursor: pointer;
  }
}

.bullets {
  z-index: 10;

  display: block;

  width: auto;
  height: 10px;
  margin: 40px auto 0;
  
  font-size: 0;
  text-align: center;

  @media (max-width: 600px) {
    margin-top: 30px;
  }

  &__item {
    display: inline-block;

    width: 10px;
    height: 10px;
    margin: 0 4px;

    border-radius: 6px;
    background: rgba(black, 0.2);

    cursor: pointer;

  }
}
